import sdk from '@common-shared/SDK';
import { Component } from 'react';

import Radio from '@hotspot-shield/shared/panel/components/radio';
import countryData from 'country-data';
import Connection from '@hotspot-shield/shared/panel/components/Connection';
import Info from '@hotspot-shield/shared/panel/components/info';

const LAST_LOCATION = 'Panel.lastLocation';

class Locations extends Component {
  constructor(props) {
    super(props);

    this.connection = new Connection();

    this.state = {
      count: controller.state.screen === 'modes' ? 3 : undefined,
      location: storage.location
    };

    const locations = sdk.proxy.getLocations('all');
    this.locations = [];
    if (locations.elite.length !== 0) {
      for (const k in locations.elite) {
        const location = locations.elite[k];

        if (locations.regular.indexOf(location) === -1) this.locations.push([location, true]);
        else this.locations.unshift([location, false]);
      }
    } else {
      for (const k in locations.regular) {
        const location = locations.regular[k];

        if (locations.regular.indexOf(location) === -1) this.locations.push([location, true]);
        else this.locations.unshift([location, false]);
      }
    }
  }

  getLocationFlag(location) {
    return `/static/assets/flags/${location.toLowerCase()}.svg`;
  }

  getLocationTitle(location) {
    if (!countryData.countries[location.toUpperCase()]) return location;

    return countryData.countries[location.toUpperCase()].name;
  }

  click(location, value, disabled) {
    if (disabled) return sdk.user.openPopUp(location);

    sdk.reporting.report('click', 'location', location, storage.location);

    storage.location = location;
    this.setState({ location });

    if (sdk.proxy.status.status === 'connected') this.connection.connect(true);
    else sdk.storage.set(LAST_LOCATION, location);
  }

  render() {
    const content = [];
    let i = 0;

    for (const k in this.locations) {
      const location = this.locations[k];

      content.push(
        <div className='row' key={k}>
          <div className='flag' style={{ backgroundImage: `url("${this.getLocationFlag(location[0])}")` }} />
          {this.getLocationTitle(location[0])}
          {location[1] && !sdk.user.status.elite && <Info text={locales.get('location_elite_only')} />}
          <Radio
            isRadio
            on={this.state.location === location[0]}
            disabled={location[1] && !sdk.user.status.elite}
            onChange={this.click.bind(this, location[0])}
          />
        </div>
      );

      if (this.state.count && ++i === this.state.count) break;
    }

    return (
      <div className='block'>
        <div className='title'>{locales.get('location_title')}</div>
        <div className='content'>
          <div className='row'>
            {locales.get('location_optimal_title')}
            <Info text={locales.get('location_auto_title')} />
            <Radio isRadio on={this.state.location === 'optimal'} onChange={this.click.bind(this, 'optimal')} />
          </div>
          {content}
          <div className='row'>
            {this.state.count && (
              <span
                onClick={() => {
                  this.setState({ count: undefined });
                }}
              >
                {locales.get('location_show_all')}
              </span>
            )}
            {!this.state.count && (
              <span
                onClick={() => {
                  this.setState({ count: 3 });
                }}
              >
                {locales.get('location_show_less')}
              </span>
            )}
          </div>
        </div>
      </div>
    );
  }
}

export default Locations;
