import sdk from '@common-shared/SDK';
import './Settings.less';

import { Component } from 'react';

import Toggle from '@hotspot-shield/shared/panel/components/toggle';

import Back from '@hotspot-shield/shared/panel/components/back';

const LANGUAGE_LIST = {
  en: 'English',
  ru: 'Русский',
  tr: 'Türkçe',
  es: 'Español',
  ko: '한국어'
};

class Settings extends Component {
  constructor(props) {
    super(props);
    this.state = {
      languageSelection: false
    };
    this.form = {
      notification: true
    };
  }

  componentDidMount() {
    sdk.reporting.report('screen', 'settings');
  }

  fieldChanged(field, value) {
    sdk.reporting.report('click', 'settings', field, value ? 'on' : 'off').then(() => {
      this.form[field] = value;
    });
  }

  changeLanguage(language) {
    locales.setLanguage(language).then(() => {
      sdk.reporting.report('click', 'settings', 'language', language).then(() => {
        this.setState({ languageSelection: false });
      });
    });
  }

  render() {
    const languages = [];

    for (const k in LANGUAGE_LIST) {
      languages.push(
        <div
          onClick={this.changeLanguage.bind(this, k)}
          className={`languageRow${locales.getActiveLanguage() === k ? ' selected' : ''}`}
          key={k}
        >
          {LANGUAGE_LIST[k]}
        </div>
      );
    }

    return (
      <div className='fullSizeScreen menuMyAccount'>
        <div className='whiteContainer'>
          <div className='title'>{locales.get('menu_settings')}</div>
          <Back onClick={() => this.props.back()} theme='dark' />
          <div className='row'>
            <div className='label' />
            <div className='content'>
              {locales.get('settings_notifications')}
              <div className='right'>
                <Toggle enabled={this.form.notification} onChange={this.fieldChanged.bind(this, 'notification')} />
              </div>
            </div>
          </div>
          <div className='row'>
            <div className='label' />
            <div className='content'>
              {locales.get('settings_language')}
              <div
                className='right'
                onClick={() => {
                  this.setState(({ languageSelection }) => ({ languageSelection: !languageSelection }));
                }}
              >
                {LANGUAGE_LIST[locales.getActiveLanguage()]} ▾
              </div>
              {this.state.languageSelection && <div className='languageSelector'>{languages}</div>}
            </div>
          </div>
        </div>
        <div className='footerVersion'>
          {locales.get('version')} {sdk.config.application.version}
          <br />
          {locales.get('powered')}
        </div>
      </div>
    );
  }
}

export default Settings;
