import sdk from '@common-shared/SDK';
import { useState, useEffect, useCallback } from 'react';
import { downloadDesktop } from 'shared/helpers/downloadDesktop';
import { CreateAccountBanner } from 'shared/panel/components/Banner/CreateAccountBanner';
import { DownloadAppBanner } from 'shared/panel/components/Banner/DownloadAppBanner';
import { sendContentInternalAnalytics } from '@common-shared/Reporting/reporting-methods';

import DataLimit from './DataLimit';

import './DataLimit.less';

const INTERVAL = 5 * 1000;
const BANNER_TYPE_CREATE_ACCOUNT = 1;
const BANNER_TYPE_APP_DOWNLOAD = 2;

const DataLimitContainer = ({ toggleMenu, changeScreen }) => {
  const { login, elite } = sdk.user.status;

  if (elite) {
    return null;
  }

  const getDailyUsage = useCallback(() => {
    return sdk.bandwidthCounter().day.bandwidth.toFixed(0);
  }, []);

  const getLimitUsage = useCallback(() => {
    return sdk.limitUsage();
  }, []);

  const disconnect = useCallback(() => {
    sdk.proxy.disconnect();
    window.panel.showScreen();
  }, []);

  const [dailyUsage, setDailyUsage] = useState(getDailyUsage());
  const [dailyLimit, setDailyLimit] = useState(getLimitUsage());
  const [amountLeft, setAmountLeft] = useState(dailyLimit - dailyUsage);
  const [bannerType, setBannerType] = useState(null);
  const [isOnceAccountCreated, setOnceAccountCreated] = useState(false);

  const recalculate = useCallback(() => {
    setDailyUsage(getDailyUsage());
    setAmountLeft(getLimitUsage() - getDailyUsage());
    setDailyLimit(sdk.limitUsage());
  }, []);

  useEffect(() => {
    recalculate();
  }, [sdk.limitUsage()]);

  useEffect(() => {
    let interval;

    if (storage.status === 'connected') {
      interval = setInterval(() => {
        recalculate();
      }, INTERVAL);
    }

    return () => clearInterval(interval);
  }, [storage.status]);

  useEffect(() => {
    if (!login && amountLeft <= 0) {
      setBannerType(BANNER_TYPE_CREATE_ACCOUNT);
      disconnect();
    } else if (login && amountLeft <= 0 && !isOnceAccountCreated) {
      setBannerType(BANNER_TYPE_APP_DOWNLOAD);
      disconnect();
    }
  }, [login, amountLeft]);

  const handleAdd = () => {
    setOnceAccountCreated(false);
    if (!login) {
      setBannerType(BANNER_TYPE_CREATE_ACCOUNT);
    } else if (login) {
      setBannerType(BANNER_TYPE_APP_DOWNLOAD);
    }
    sendContentInternalAnalytics('ui_click', {
      placement: 'scn_dashboard',
      action_name: 'bnr_mb_limit',
      action_detail: `mb_left: ${amountLeft}`
    });
  };

  const handleCreateAccountBannerBtn = e => {
    e.preventDefault();
    sendContentInternalAnalytics('ui_click', {
      placement: 'scn_add_more_mb_500',
      action_name: 'btn_create_account',
      action_detail: `mb_left: ${amountLeft}`
    });
    toggleMenu();
    changeScreen('signUp');
    setBannerType(null);
    setOnceAccountCreated(true);
  };

  const handleAppDownloadBannerBtn = e => {
    e.preventDefault();
    sendContentInternalAnalytics('ui_click', {
      placement: 'scn_add_more_mb_1000',
      action_name: 'btn_install_hss',
      action_detail: `mb_left: ${amountLeft}`
    });
    sdk.storage.set('appDownloaded', true);
    downloadDesktop();
  };
  const handleGoUnlimitedWithPremiumBtn = (e, screen) => {
    e.preventDefault();
    sendContentInternalAnalytics('ui_click', {
      placement: screen,
      action_name: 'btn_go_unlimited',
      action_detail: `mb_left: ${amountLeft}`
    });
    sdk.user.openPopUp('mainBuyElite', sdk.tabs.active.domain);
  };
  const handleBannerCloseBtn = (e, screen) => {
    e.preventDefault();
    setBannerType(null);
    sendContentInternalAnalytics('ui_click', {
      placement: screen,
      action_name: 'btn_close',
      action_detail: `mb_left: ${amountLeft}`
    });
  };

  useEffect(() => {
    if (bannerType === BANNER_TYPE_CREATE_ACCOUNT) {
      sendContentInternalAnalytics('ui_view', {
        placement: 'scn_add_more_mb_500',
        action_detail: `mb_left: ${amountLeft}`
      });
    } else if (bannerType === BANNER_TYPE_APP_DOWNLOAD) {
      sendContentInternalAnalytics('ui_view', {
        placement: 'scn_add_more_mb_1000',
        action_detail: `mb_left: ${amountLeft}`
      });
    }
  }, [bannerType]);

  let banner = null;

  switch (bannerType) {
    case BANNER_TYPE_CREATE_ACCOUNT:
      banner = (
        <CreateAccountBanner
          btnClick={handleCreateAccountBannerBtn}
          onClose={e => handleBannerCloseBtn(e, 'scn_add_more_mb_500')}
          premiumBtnClick={e => handleGoUnlimitedWithPremiumBtn(e, 'scn_add_more_mb_500')}
        />
      );
      break;

    case BANNER_TYPE_APP_DOWNLOAD:
      banner = (
        <DownloadAppBanner
          btnClick={handleAppDownloadBannerBtn}
          onClose={e => handleBannerCloseBtn(e, 'scn_add_more_mb_1000')}
          premiumBtnClick={e => handleGoUnlimitedWithPremiumBtn(e, 'scn_add_more_mb_1000')}
        />
      );
      break;

    default:
      break;
  }

  return (
    <>
      <DataLimit total={dailyLimit} amount={amountLeft} onClick={handleAdd} isLow={amountLeft < 20} />
      {banner}
    </>
  );
};

export default DataLimitContainer;
