import sdk from '@common-shared/SDK';
import './Trial.less';

import { useEffect } from 'react';

function Trial({ hide }) {
  useEffect(() => {
    sdk.reporting.report('promo', 'trial', 'show');
  }, []);

  const close = () => {
    sdk.reporting.report('promo', 'trial', 'close');
    hide();
  };

  const start = () => {
    sdk.reporting.report('promo', 'trial', 'start');
    sdk.user.openTrialPromo('7dTrialoffer', sdk.tabs.active.domain);
  };

  return (
    <div className='trialPromo'>
      <div className='family' />
      <div className='oneTime'>{locales.get('trial_onetime')}</div>
      <div className='title'>{locales.get('trial_title')}</div>
      <div className='row'>{locales.get('trial_row_1')}</div>
      <div className='row'>{locales.get('trial_row_2')}</div>
      <div className='row'>{locales.get('trial_row_3')}</div>
      <div className='row'>{locales.get('trial_row_4')}</div>
      <div className='row'>{locales.get('trial_row_5')}</div>
      <div className='upgrade blueButton' onClick={start}>
        {locales.get('trial_upgrade')}
      </div>
      <div className='continue'>
        <span onClick={close}>{locales.get('trial_thanks')}</span>
      </div>
    </div>
  );
}

export default Trial;
