import { Component } from 'react';
import sdk from '@common-shared/SDK';

class Timer extends Component {
  constructor() {
    super();

    const startTime = sdk.proxy.timer();
    this.startTime = startTime || Date.now();

    this.state = {
      timer: this.getTimeString()
    };

    this.interval = setInterval(() => {
      this.setState({ timer: this.getTimeString() });
    }, 1000);
  }

  getTimeString() {
    let time = Math.round((Date.now() - this.startTime) / 1000);
    let hours = 0;
    let minutes = 0;
    let seconds = 0;

    hours = (time / 3600) | 0;
    hours = hours > 9 ? hours : `0${hours}`;
    time -= hours * 3600;

    minutes = (time / 60) | 0;
    minutes = minutes > 9 ? minutes : `0${minutes}`;
    time -= minutes * 60;

    seconds = time > 9 ? time : `0${time}`;

    return `${hours}:${minutes}:${seconds}`;
  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }

  render() {
    return <div className='timer'>{this.state.timer}</div>;
  }
}

export default Timer;
