import './Location.less';
import { Component } from 'react';
import ReactDOM from 'react-dom';
import countryData from 'country-data';

import Back from '@hotspot-shield/shared/panel/components/back';
import map from './map';

class Location extends Component {
  constructor(props) {
    super();

    this.state = {
      dot: {
        show: false,
        x: 0,
        y: 0
      },
      i: 0
    };
  }

  locationClick() {
    this.props.toggleSettings();
  }

  getLocationTitle(location) {
    if (location === 'optimal') return locales.get('map_optimal');

    if (!countryData.countries[location.toUpperCase()]) return location;
    return countryData.countries[location.toUpperCase()].name;
  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }

  updateDot() {
    if (storage.location !== 'optimal') return;
    const dot = {
      show: false,
      x: 0,
      y: 0
    };

    let x = 1;
    let y = 1;
    const s = 1;

    const dots = [];

    for (const i in map) {
      for (const j in map[i]) {
        const pixel = map[i][j];
        if (pixel !== 0) {
          if (pixel.indexOf('_a') !== -1) {
            dots.push([x - 4, y - 4]);
          }
        }

        x += s * 3;
      }
      x = s;
      y += s * 3;
    }

    const d = dots[Math.floor(Math.random() * dots.length)];
    dot.show = true;
    dot.x = d[0];
    dot.y = d[1];

    this.setState({ dot });
  }

  componentDidMount() {
    // eslint-disable-next-line react/no-find-dom-node,react/no-string-refs
    const canvas = ReactDOM.findDOMNode(this.refs.mainMap);
    const ctx = canvas.getContext('2d');
    const location = `${storage.location.toUpperCase()}_a`;

    const dot = {
      show: false,
      x: 0,
      y: 0
    };

    let x = 1;
    let y = 1;
    const s = 1;

    const dots = [];

    for (const i in map) {
      for (const j in map[i]) {
        const pixel = map[i][j];
        if (pixel !== 0) {
          ctx.beginPath();
          ctx.fillStyle = '#3D6785';
          if (pixel === location) {
            dot.x = x - 4;
            dot.y = y - 4;
            dot.show = true;
          }
          if (pixel.indexOf('_a') !== -1) {
            dots.push([x - 4, y - 4]);
          }
          ctx.fillRect(x, y, 1, 1);
          ctx.closePath();
        }

        x += s * 3;
      }
      x = s;
      y += s * 3;
    }

    /* if(storage.location === 'optimal') {
      let d = dots[Math.floor(Math.random()*dots.length)];
      dot.show = true;
      dot.x = d[0];
      dot.y = d[1];

      this.interval = setInterval(() => {this.updateDot()}, 5000);
    } */

    this.setState({ dot });
  }

  render() {
    return (
      <div id='mainLocation' className={`topBorder${storage.firstAnimation}`}>
        <div className='title'>
          <div className='text'>{locales.get('map_title')}</div>
          <div className='selector' onClick={this.locationClick.bind(this)}>
            {this.getLocationTitle(storage.location)}
            <Back theme='white' />
          </div>
        </div>
        <div className='canvasContainer' onClick={this.locationClick.bind(this)}>
          {this.state.dot.show && (
            <div className='dot' style={{ left: `${this.state.dot.x}px`, top: `${this.state.dot.y}px` }}>
              <div className='circle' />
            </div>
          )}
          {/* eslint-disable-next-line react/no-string-refs */}
          <canvas className='mainMap' ref='mainMap' width='360' height='200' />
        </div>
      </div>
    );
  }
}

export default Location;
