import sdk from '@common-shared/SDK';
import './Promo.less';
import { Component } from 'react';

import RateUs from './RateUs';
import Trial from './Trial';

const DATA = 'Promo.data';

class Promo extends Component {
  constructor() {
    super();

    this.state = {
      screen: 'hide'
    };
  }

  componentDidMount() {
    const data = sdk.storage.get(DATA) ? sdk.storage.get(DATA) : {};

    if (!data.trial) data.trial = { done: false, next: Date.now() + sdk.config.promo.trial.delay, count: 0 };
    if (!data.rateUs) data.rateUs = { done: false, next: Date.now() + sdk.config.promo.rateUs.delay, count: 0 };

    sdk.storage.set(DATA, data).then(() => {
      this.data = data;
      this.shouldShow();
    });
  }

  shouldShow() {
    const elite = sdk.user.status && sdk.user.status.elite;
    if (
      !elite &&
      !this.data.trial.done &&
      this.data.trial.next < Date.now() &&
      this.data.trial.count < sdk.config.promo.trial.limit
    ) {
      this.data.trial.next = Date.now() + sdk.config.promo.trial.interval;
      this.data.trial.count++;
      sdk.storage.set(DATA, this.data);
      return this.setState({ screen: 'trial' });
    }

    if (!this.data.rateUs.done && this.data.rateUs.next < Date.now()) {
      this.data.rateUs.next = Date.now() + sdk.config.promo.rateUs.interval;
      this.data.rateUs.count++;
      sdk.storage.set(DATA, this.data);
      return this.setState({ screen: 'rateUs' });
    }
  }

  hide() {
    this.setState({ screen: 'hide' });
  }

  rated() {
    this.setState({ screen: 'hide' });
  }

  render() {
    let content = [];

    const screen = this.state.screen;

    if (screen === 'trial') content = <Trial hide={this.hide.bind(this)} />;
    if (screen === 'rateUs')
      content.push(
        <div className='blur' key='blur' />,
        <RateUs hide={this.hide.bind(this)} rated={this.rated.bind(this)} />
      );

    if (!content) return <div />;

    return <div id='mainPromo'>{content}</div>;
  }
}

export default Promo;
