import sdk from '@common-shared/SDK';
import { t } from '@common-shared/Translation';
import './Survey.less';

export const Survey = ({ onClose, onClick }) => {
  return (
    <section className='survey'>
      <div className='survey__close-sign' onClick={onClose}>
        <img src='/static/assets/icons/close.svg' />
      </div>
      <div>
        <img src='/static/assets/survey/Survey.png' />
      </div>
      <section className='survey__text_group'>
        <p className='survey__title'>{t('survey_title')}</p>
        <p className='survey__description'>{t('survey_description')}</p>
      </section>
      <div className='survey__button' onClick={onClick}>
        {t('survey_button')}
      </div>
    </section>
  );
};

export const SurveyScreen = props => {
  const onClose = async () => {
    await sdk.storage.set('Panel.surveyShowed', true);
    props.onClose?.();
  };
  const onClick = async () => {
    const clientId = sdk.user.hash;
    await sdk.storage.set('Panel.surveyShowed', true);
    await sdk.tabs.openPage(`https://www.surveymonkey.com/r/LDMD356?d=${clientId}`);
    props.onClick?.();
  };
  return <Survey onClose={onClose} onClick={onClick} />;
};
