import sdk from '@common-shared/SDK';

const LAST_LOCATION = 'Panel.lastLocation';

class Connection {
  connect(noReload) {
    sdk.reporting.report(
      'connection',
      noReload ? 'reconnected' : 'connected',
      sdk.user.status.elite ? 'elite' : 'free'
    );
    return sdk.storage.set(LAST_LOCATION, storage.location).then(() => {
      return sdk.proxy.connect(storage.location, noReload);
    });
  }

  disconnect() {
    sdk.reporting.report('connection', 'disconnected', sdk.user.status.elite ? 'elite' : 'free');
    return sdk.proxy.disconnect();
  }
}

export default Connection;
