import sdk from '@common-shared/SDK';
import './MyAccount.less';

import { Component } from 'react';

import Back from '@hotspot-shield/shared/panel/components/back';

class MyAccount extends Component {
  componentDidMount() {
    sdk.reporting.report('screen', 'myaccount');
  }

  render() {
    const username =
      sdk.user.status.login.length > 20 ? `${sdk.user.status.login.substr(0, 20)}...` : sdk.user.status.login;

    return (
      <div className='fullSizeScreen menuMyAccount'>
        <div className='whiteContainer'>
          <div className='title'>{locales.get('menu_account')}</div>
          <Back onClick={() => this.props.back()} theme='dark' />
          <div className='row'>
            <div className='label'>{locales.get('account_type')}</div>
            <div className='content'>
              Hotspot Shield
              {sdk.user.status.elite && <div className='blueLabel'>{locales.get('elite')}</div>}
              {!sdk.user.status.elite && (
                <div
                  className='right'
                  onClick={() => {
                    sdk.user.openPopUp('upgradeMyAccount');
                  }}
                >
                  <img src='/static/assets/menu/blueStar.png' />
                  {locales.get('account_upgrade')}
                </div>
              )}
            </div>
          </div>
          <div className='row'>
            <div className='label'>{locales.get('account_username')}</div>
            <div className='content'>
              {username}
              <div className='right' onClick={() => this.props.changeScreen('signIn')}>
                {locales.get('account_switch')}
              </div>
            </div>
          </div>
          <div className='row'>
            <div className='label'>{locales.get('account_key')}</div>
            <div className='content'>
              {sdk.user.status.elite ? locales.get('account_activated') : locales.get('account_na')}
              {!sdk.user.status.elite && (
                <div
                  className='right'
                  onClick={() => {
                    sdk.reporting.report('elite', 'redeem', 'myAccount', sdk.tabs.active.domain).then(() => {
                      sdk.tabs.openPage('https://account.hotspotshield.com/redeem-code');
                    });
                  }}
                >
                  {locales.get('account_redeem')}
                </div>
              )}
            </div>
          </div>
        </div>
        <div className='footerVersion'>
          {locales.get('version')} {sdk.config.application.version}
          <br />
          {locales.get('powered')}
        </div>
      </div>
    );
  }
}

export default MyAccount;
