import sdk from '@common-shared/SDK';
import './RestorePassword.less';

import { Component } from 'react';

import Input from '@hotspot-shield/shared/panel/components/input';

import Back from '@hotspot-shield/shared/panel/components/back';

class RestorePassword extends Component {
  constructor(props) {
    super(props);

    this.state = {
      form: {
        email: false
      },
      disabled: false,
      message: ''
    };

    this.form = {
      email: ''
    };
  }

  componentDidMount() {
    sdk.reporting.report('screen', 'restorepassword');
  }

  fieldChanged(field, value) {
    this.form[field] = value.trim();
  }

  submitForm() {
    const state = this.state;
    state.form = {
      email: false
    };
    state.disabled = false;
    state.message = '';

    let valid = true;
    if (this.form.email === '') {
      state.form.email = true;
      valid = false;
      state.message = locales.get('fill');
    }

    if (!valid) {
      return this.setState(state);
    }

    state.disabled = true;

    this.setState(state, () => {
      const state = this.state;
      state.disabled = false;

      sdk.user
        .restorePassword(this.form.email)
        .then(() => this.setState(state, () => this.props.changeScreen('signIn')))
        .catch(response => {
          state.message = response.error_message;
          state.form = {
            email: true
          };

          return this.setState(state);
        });
    });
  }

  render() {
    return (
      <div className='fullSizeScreen menuRestorePassword'>
        <div className='title'>{locales.get('restore_forgot')}</div>
        <Back onClick={() => this.props.back()} theme='dark' />
        {this.state.message !== '' && <div className='message'>{this.state.message}</div>}
        <div className='logo' />
        <div className='email'>
          <Input
            label={locales.get('label_email')}
            value={this.form.email}
            notValid={this.state.form.email}
            onEnter={this.submitForm.bind(this)}
            disabled={this.state.disabled}
            onChange={this.fieldChanged.bind(this, 'email')}
          />
        </div>
        {this.state.disabled && <div className='restorePasswordBtn disabled blueButton'>{locales.get('wait')}</div>}
        {!this.state.disabled && (
          <div className='restorePasswordBtn blueButton' onClick={this.submitForm.bind(this)}>
            {locales.get('restore')}
          </div>
        )}
      </div>
    );
  }
}

export default RestorePassword;
