import sdk from '@common-shared/SDK';
import '@common-shared/Utils/CrossBrowserVaribles';
import './panel.less';

import Debug from '@common-shared/Wdgt/Debug';
import Signal from '@common-shared/Wdgt/Signal';
import ReactDOM from 'react-dom';
import { hasOtherProxyExtensions } from '@common-shared/Utils/ExtensionManagment';
import { isFirefox } from '@common-shared/BrowserDetection';
import Controller from './Controller';

import locales from './components/_locales';

const PANEL_LOADED = 'Panel.loaded';
const PANEL_ANIMATED = 'Panel.animated';
const LAST_LOCATION = 'Panel.lastLocation';
const TOOLTIPS_SHOWED = 'Panel.tooltipsShowed';

class Panel {
  constructor() {
    this.init();
  }

  init() {
    window.signals = {
      onLoaded: new Signal(),
      onStatusChanged: new Signal()
    };

    const init = () => {
      const page = document.getElementById('page');
      window.sdk = browser.extension.getBackgroundPage()?.sdk;
      if (!page || !sdk?.proxy || !sdk?.tabs)
        return setTimeout(() => {
          init();
        }, 50);

      window.locales = new locales();
      window.storage = {};
      new Debug();

      this.report();
      window.panel = this;
      // eslint-disable-next-line react/no-render-return-value
      window.controller = ReactDOM.render(<Controller />, page);
      this.start();
    };

    init();
  }

  report() {
    const domain = sdk.tabs.active.domain;
    let status = sdk.proxy.status.status;

    if (sdk.proxy.getGlobalStatus() === 'unavailable') {
      status = 'unavailable';
    } else if (sdk.proxy.getGlobalStatus() === 'desktop') {
      status = 'desktop';
    } else if (sdk.proxy.bypass.isInList(domain) && status !== 'disconnected') {
      status = 'bypass';
    } else if (sdk.proxy.secured.isInList(domain) && status === 'disconnected') {
      status = 'secured';
    }

    return sdk.reporting.report('application', 'open', status);
  }

  start() {
    storage.panelAnimated = sdk.storage.get(PANEL_ANIMATED);

    if (!sdk.loading?.loaded)
      return setTimeout(() => {
        this.start();
      }, 500);
    storage.panelLoaded = sdk.storage.get(PANEL_LOADED);

    this.showScreen();
  }

  async showScreen() {
    let screen = 'main';
    const status = sdk.proxy.status;
    const lastLocation = sdk.storage.get(LAST_LOCATION);
    let globalStatus = sdk.proxy.getGlobalStatus();
    if (await hasOtherProxyExtensions()) {
      globalStatus = 'unavailable';
    }
    const shouldShowSurvey = sdk.config.survey.enable && !sdk.storage.get('Panel.surveyShowed');
    const tooltipsShowed = sdk.storage.get(TOOLTIPS_SHOWED);

    storage.status = status.status;
    storage.location = status.status === 'connected' ? status.location : lastLocation || 'optimal';

    storage.bypass = sdk.proxy.bypass.isInList(sdk.tabs.active.domain);
    storage.secured = sdk.proxy.secured.isInList(sdk.tabs.active.domain);

    if (globalStatus === 'desktop') screen = 'desktop';
    else if (!isFirefox && globalStatus === 'unavailable') screen = 'unavailable';
    else if (!tooltipsShowed) screen = 'tooltips';
    else if (shouldShowSurvey && window.controller.lastScreen !== 'tooltips') screen = 'survey';

    if (!storage.panelLoaded) {
      storage.panelLoaded = true;
      sdk.storage.set(PANEL_LOADED, true);

      return signals.onLoaded.dispatch(screen);
    }

    controller.setState({ screen });
  }
}

new Panel();
