import './radio.less';

import { useEffect } from 'react';
import { useToggle } from 'react-use';

function Radio({ on = false, isRadio = false, disabled = false, onChange = () => {} }) {
  const [isToggled, toggle] = useToggle(on);
  useEffect(() => {
    toggle(on);
  }, [on]);
  const onClick = () => {
    if (disabled) {
      return;
    }
    if (isRadio && isToggled) return;
    onChange(!isToggled, false);
    toggle(!isToggled);
  };

  return (
    <div
      className={`radio ${disabled ? 'disabled' : (isToggled ? 'on' : 'off') + (isRadio ? ' isRadio' : '')}`}
      onClick={onClick}
    />
  );
}

export default Radio;
