import sdk from '@common-shared/SDK';
import './Unavailable.less';
import { Component } from 'react';

class Unavailable extends Component {
  click() {
    sdk.reporting.report('click', 'unavailable');

    sdk.proxy.disableOtherProxy().then(() => {
      return panel.showScreen();
    });
  }

  render() {
    return (
      <div id='screenUnavailable'>
        <div className='title'>{locales.get('unavaliable_title')}</div>
        <div className='description'>{locales.get('unavaliable_description')}</div>
        <div className='button blueButton' onClick={this.click.bind(this)}>
          {locales.get('unavaliable_disable')}
        </div>
      </div>
    );
  }
}

export default Unavailable;
