import sdk from '@common-shared/SDK';
import './RateUs.less';
import { Component } from 'react';

class RateUs extends Component {
  constructor(props) {
    super(props);

    this.state = {
      state: 'rateUs'
    };
  }

  close() {
    this.props.hide();
  }

  closeClick() {
    sdk.reporting.report('promo', 'rateus', 'close');
    this.close();
  }

  click(raiting) {
    sdk.reporting.report('promo', 'rateus', `star-${raiting}`);
    if (raiting <= 3) {
      this.props.hide();
    } else {
      sdk.tabs.openPage(sdk.config.promo.rateUs.url);
    }
  }

  report() {
    switch (this.state.state) {
      case 'rateUs':
        sdk.reporting.report('promo', 'rateus', 'show');
        break;
      case 'thankYou':
        sdk.reporting.report('promo', 'rateus', 'thankyou');
        break;
      case 'redirect':
        sdk.reporting.report('promo', 'rateus', 'redirect');
        break;
    }
  }

  componentDidMount() {
    this.report();
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevState.state !== this.state.state) {
      this.report();
    }
  }

  render() {
    let content;
    switch (this.state.state) {
      case 'rateUs':
        content = (
          <div className='rateUs'>
            <div className='title'>{locales.get('rate_us_rate_title')}</div>
            <div className='text'>{locales.get('rate_us_rate_text')}</div>
            <div className='stars'>
              {[1, 2, 3, 4, 5].reverse().map(rating => {
                return <div className='star' key={rating} onClick={this.click.bind(this, rating)} />;
              })}
            </div>
          </div>
        );
        break;
      case 'thankYou':
        content = (
          <div className='thankYou'>
            <div className='title'>{locales.get('rate_us_thank_you')}</div>
          </div>
        );
        break;
      case 'redirect':
        content = (
          <div className='redirect'>
            <div className='title'>{locales.get('rate_us_redirect_1')}</div>
            <div className='text'>{locales.get('rate_us_redirect_2')}</div>
          </div>
        );
        break;
    }

    return (
      <div className='block rate'>
        <div className='close' onClick={this.closeClick.bind(this, 'close')} />
        {content}
      </div>
    );
  }
}

export default RateUs;
