import sdk from '@common-shared/SDK';
import { Component } from 'react';
import { t } from '@common-shared/Translation';
import Toggle from '@hotspot-shield/shared/panel/components/toggle';
import Info from '@hotspot-shield/shared/panel/components/info';

const PROXY_LOCAL = 'Proxy.proxyLocal';

class Modes extends Component {
  constructor(props) {
    super(props);

    this.form = {
      sword: sdk.sword.getStatus(),
      adBlock: sdk.adBlocker.getStatus(),
      tracker: sdk.trackerBlocker.getStatus(),
      malware: sdk.malwareBlocker.getStatus(),
      cookie: sdk.cookiesBlocker.getStatus(),
      proxyLocal: sdk.storage.get(PROXY_LOCAL) ? 'on' : 'off',
      webRTC: sdk.webRTC.getStatus()
    };

    this.texts = {
      sword: locales.get('modes_sword'),
      tracker: locales.get('modes_tracker'),
      adBlock: locales.get('modes_adblock'),
      malware: locales.get('modes_malware'),
      cookie: locales.get('modes_cookies'),
      proxyLocal: locales.get('modes_proxyLocal'),
      webRTC: locales.get('modes_webRTC')
    };

    this.help = {
      sword: locales.get('modes_tooltips_sword'),
      tracker: locales.get('modes_tooltips_tracker'),
      adBlock: locales.get('modes_tooltips_adBlock'),
      malware: locales.get('modes_tooltips_malware'),
      cookie: locales.get('modes_tooltips_cookie'),
      proxyLocal: locales.get('modes_tooltips_proxyLocal'),
      webRTC: locales.get('modes_tooltips_webRTC')
    };

    this.callbacks = {
      sword: sdk.sword.setStatus,
      adBlock: sdk.adBlocker.setStatus,
      tracker: sdk.trackerBlocker.setStatus,
      malware: sdk.malwareBlocker.setStatus,
      cookie: sdk.cookiesBlocker.setStatus,
      webRTC: sdk.webRTC.setStatus,
      proxyLocal: this.changeDnsResolve.bind(this)
    };
  }

  changeDnsResolve() {
    if (!sdk.storage.get(PROXY_LOCAL)) {
      sdk.storage.set(PROXY_LOCAL, true).then(() => {
        sdk.proxy.render();
      });
    } else {
      sdk.storage.set(PROXY_LOCAL, false).then(() => {
        sdk.proxy.render();
      });
    }
  }

  toggleMode(mode, state) {
    state = state ? 'on' : 'off';
    sdk.reporting.report('click', 'mode', mode, state);
    this.callbacks[mode](state);
  }

  render() {
    const content = [];

    for (const k in this.texts) {
      content.push(
        <div key={k} className='row'>
          {k === 'sword' && <div className='highlight' />}
          {this.texts[k]}
          <Info text={locales.get(`modes_tooltips_${k}`)} />
          <div className='beta'>{locales.get('beta')}</div>
          {this.form[k] !== 'link' && (
            <Toggle enabled={this.form[k] === 'on'} onChange={this.toggleMode.bind(this, k)} />
          )}
          {this.form[k] === 'link' && <div className='right'>{t('modes_install')}</div>}
        </div>
      );
    }

    return (
      <div className='block'>
        <div className='title'>{locales.get('modes_title')}</div>
        <div className='content'>{content}</div>
      </div>
    );
  }
}

export default Modes;
