import sdk from '@common-shared/SDK';
import './Statistic.less';
import { Component } from 'react';

const INTERVAL = 5 * 1000;

class Statistic extends Component {
  constructor() {
    super();

    this.state = {
      ads: sdk.adBlocker.getCounter(),
      trackers: sdk.trackerBlocker.getCounter(),
      malware: sdk.malwareBlocker.getCounter(),
      cookies: sdk.cookiesBlocker.getCounter()
    };

    this.interval = setInterval(() => {
      this.setState({
        ads: sdk.adBlocker.getCounter(),
        trackers: sdk.trackerBlocker.getCounter(),
        malware: sdk.malwareBlocker.getCounter(),
        cookies: sdk.cookiesBlocker.getCounter()
      });
    }, INTERVAL);
  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }

  render() {
    return (
      <div id='mainStatistic' className={`topBorder${storage.firstAnimation}`}>
        <div className='title'>{locales.get('statistic_title')}</div>
        <div className='content'>
          <div className='element'>
            <div className='number'>{this.state.ads}</div>
            <div className='text'>{locales.get('statistic_ads')}</div>
          </div>
          <div className='element'>
            <div className='number'>{this.state.malware}</div>
            <div className='text'>{locales.get('statistic_malware')}</div>
          </div>
          <div className='element'>
            <div className='number'>{this.state.cookies}</div>
            <div className='text'>{locales.get('statistic_cookies')}</div>
          </div>
          <div className='element'>
            <div className='number'>{this.state.trackers}</div>
            <div className='text'>{locales.get('statistic_trackers')}</div>
          </div>
        </div>
      </div>
    );
  }
}

export default Statistic;
