import sdk from '@common-shared/SDK';
import './Help.less';

import { Component } from 'react';

import Back from '@hotspot-shield/shared/panel/components/back';
import { t } from '@common-shared/Translation';

class Help extends Component {
  componentDidMount() {
    sdk.reporting.report('screen', 'help');
  }

  click(affr) {
    const urls = {
      tutorial: browser.runtime.getURL('/pages/help.html'),
      payments: 'https://support.hotspotshield.com/hc/en-us/categories/200189964-Payments-Subscriptions',
      manage: 'https://support.hotspotshield.com/hc/en-us/categories/200299824-Manage-Account-Devices',
      support: `https://support.hotspotshield.com/hc/en-us`,
      other: 'https://support.hotspotshield.com/hc/en-us',
      contact: 'https://support.hotspotshield.com/hc/en-us/requests/new'
    };

    sdk.reporting.report('click', 'help', affr).then(() => {
      sdk.tabs.openPage(urls[affr]);
    });
  }

  render() {
    const data = [
      [locales.get('help_tutorial'), 'tutorial'],
      [locales.get('help_payments'), 'payments'],
      [locales.get('help_manage'), 'manage'],
      [locales.get('help_support'), 'support'],
      [locales.get('help_other'), 'other'],
      [locales.get('help_contact'), 'contact']
    ];
    const content = [];

    for (const k in data) {
      content.push(
        <div className='row' key={k} onClick={this.click.bind(this, data[k][1])}>
          <div className='label' />
          <div className='content'>
            {data[k][0]}
            <div className='right'>{t('help_open_button')}</div>
          </div>
        </div>
      );
    }

    return (
      <div className='fullSizeScreen menuHelp'>
        <div className='whiteContainer'>
          <div className='title'>{locales.get('menu_help')}</div>
          <Back onClick={() => this.props.back()} theme='dark' />
          {content}
        </div>
        <div className='footerVersion'>
          {locales.get('version')} {sdk.config.application.version}
          <br />
          {locales.get('powered')}
        </div>
      </div>
    );
  }
}

export default Help;
