import sdk from '@common-shared/SDK';
import './BuyElite.less';

function BuyElite() {
  if (sdk.user.status.elite) return <div />;

  return (
    <div id='mainBuyElite' onClick={sdk.user.openPopUp.bind(this, 'mainBuyElite', sdk.tabs.active.domain)}>
      <div className='star' />
      <div>{locales.get('buy_elite')}</div>
    </div>
  );
}

export default BuyElite;
