import sdk from '@common-shared/SDK';
import './Main.less';
import { Component } from 'react';
import { sendContentInternalAnalytics } from '@common-shared/Reporting/reporting-methods';

import { DataLimit } from 'shared/panel/components/data-limit';
import Menu from '../Menu';
import Settings from '../Settings';
import Promo from './Promo';
import Header from './Header';
import Button from './Button';
import BuyElite from './BuyElite';
import Location from './Location';
import Statistic from './Statistic';
import Unblocked from './Unblocked';
import Connection from './Connection';
import BypassWebsites from './BypassWebsites';
import SecuredWebsites from './SecuredWebsites';

const PANEL_ANIMATED = 'Panel.animated';

class Main extends Component {
  constructor() {
    super();

    this.state = {
      i: 0,
      menu: false,
      settings: false,
      screenName: ''
    };

    this.signal = signals.onStatusChanged.add(() => {
      this.setState(({ i }) => ({ i: i + 1 }));
    });
  }

  componentWillUnmount() {
    this.signal.detach();
  }

  toggleMenu = () => {
    this.setState(({ menu }) => ({ menu: !menu }));
  };

  toggleSettings(screen) {
    screen = screen || 'locations';
    this.setState(({ settings }) => ({ settings: !settings, settingsScreen: screen }));
  }

  changeScreen = screenName => {
    this.setState({ screenName });
  };

  render() {
    const scroll = storage.status === 'connected' ? 'scroll' : '';
    sendContentInternalAnalytics('ui_view', { placement: 'scn_dashboard' });
    return (
      <div id='screenMain'>
        <Header toggleMenu={this.toggleMenu} toggleSettings={this.toggleSettings.bind(this, 'modes')} />
        <Menu
          show={this.state.menu}
          toggleMenu={this.toggleMenu}
          screenName={this.state.screenName}
          resetScreenName={() => this.changeScreen('')}
        />
        <Settings
          show={this.state.settings}
          screen={this.state.settingsScreen}
          toggleSettings={this.toggleSettings.bind(this)}
        />
        {!sdk.user.status.elite && <DataLimit changeScreen={this.changeScreen} toggleMenu={this.toggleMenu} />}
        <div id='content' className={scroll + (sdk.user.status.elite ? ' elite' : ' free')}>
          {this.content()}
        </div>
        <BuyElite />
      </div>
    );
  }

  content() {
    storage.firstAnimation = '';

    if (storage.status === 'connected' && !storage.panelAnimated) {
      storage.firstAnimation = ' firstAnimation';
      storage.panelAnimated = true;
      sdk.storage.set(PANEL_ANIMATED, true);
    }

    if (storage.status === 'connected' && storage.bypass) {
      return [
        <Button key='Button' />,
        <BypassWebsites key='bypassWebsites' />,
        <Connection key='connection' />,
        <Statistic key='statistic' />,
        <Location key='location' toggleSettings={this.toggleSettings.bind(this, 'locations')} />,
        <Unblocked key='unblocked' />,
        <SecuredWebsites key='securedWebsites' />,
        <Promo key='promo' />,
        <div key='margin' style={{ height: '80px' }} />
      ];
    }

    if (storage.status === 'disconnected' && storage.secured) {
      return [<SecuredWebsites mainScreen='true' key='securedWebsites' />, <Button key='button' />];
    }

    if (storage.status !== 'connected') {
      return <Button />;
    }

    return [
      <Button key='button' />,
      <Connection key='connection' />,
      <Statistic key='statistic' />,
      <Location key='location' toggleSettings={this.toggleSettings.bind(this, 'locations')} />,
      <Unblocked key='unblocked' />,
      <SecuredWebsites key='securedWebsites' />,
      <BypassWebsites key='bypassWebsites' />,
      <Promo key='promo' />,
      <div key='margin' style={{ height: '80px' }} />
    ];
  }
}

export default Main;
