import sdk from '@common-shared/SDK';
import { useEffect } from 'react';
import isBoolean from 'lodash/isBoolean';
import './DataConsent.less';
import { DATA_CONSENT_ACCEPTED } from '@common-shared/Utils/StorageKeys';

export function DataConsent({ onClose, onReviewSettings }) {
  useEffect(() => {
    if (!isBoolean(sdk.storage.get(DATA_CONSENT_ACCEPTED))) {
      sdk.storage.set(DATA_CONSENT_ACCEPTED, false);
    }
  }, []);

  return (
    <div className='data-consent'>
      <section className='data-consent__content'>
        <div>
          <svg width='64' height='64' viewBox='0 0 64 64' fill='none' xmlns='http://www.w3.org/2000/svg'>
            <path
              fillRule='evenodd'
              clipRule='evenodd'
              d='M3 3V26C3 38.7026 13.2975 49 26 49C38.7026 49 49 38.7026 49 26V3H3ZM52 0V26C52 40.3594 40.3594 52 26 52C11.6406 52 0 40.3594 0 26V0H52Z'
              fill='white'
            />
            <path
              fillRule='evenodd'
              clipRule='evenodd'
              d='M38 12H12V38C12 52.3594 23.6406 64 38 64C52.3594 64 64 52.3594 64 38V12H38Z'
              fill='#2FA6F8'
            />
            <path
              fillRule='evenodd'
              clipRule='evenodd'
              d='M2 2V26C2 39.2548 12.7452 50 26 50C39.2548 50 50 39.2548 50 26V2H2ZM52 0V26C52 40.3594 40.3594 52 26 52C11.6406 52 0 40.3594 0 26V0H52Z'
              fill='#101010'
            />
            <path
              d='M29.364 32.4349L37.8492 23.9497L39.2635 25.3639L29.364 35.2634L23 28.8994L24.4142 27.4852L29.364 32.4349Z'
              fill='white'
            />
          </svg>
        </div>

        <h1>We’re commited to your privacy</h1>
        <p>Hotspot Shield collects the following user data in order to deliver the best VPN service possible:</p>
        <p>
          Usage information. We collect information about how you interact with our services, such as how often you use
          our services, how much bandwidth you use, and when and for how long you use our services.
        </p>
        <p>
          Device information. We collect information from and about the device you use to access our services, including
          about the browsers and Aura apps you use to access our services. For example, we may collect device
          identifiers, browser types, device types and settings, operating system versions, mobile, wireless, and other
          network information (such as internet service provider name, carrier name and signal strength), and
          application version numbers.
        </p>
        <p>
          Diagnostic information. We may collect information about the nature of the requests that you make to our
          servers (such as what is being requested, information about the device and app used to make the request,
          timestamps, and referring URLs). However, our VPN products do not log any information that associates your
          identity with your VPN browsing activity. We do not maintain any records that show what you were browsing or
          accessing through a VPN connection. See the VPN Products Privacy Notice for more information.
        </p>
        <p>
          Location information. Unless otherwise expressly stated, we do not collect your location information based on
          your device’s GPS or other device sensor data. However, we may collect your approximate location by
          calculating an imprecise latitude and longitude based on your IP address to provide you with better service
          (e.g. to connect you to the nearest and fastest VPN server).
        </p>
        <p>
          Account information. Some services require or allow you to create an account before you can access them. As
          part of registering for an account, we may collect information such as your name, username, email address,
          password and certain other information from you.
        </p>
        <p>
          Billing and payment information. In order to purchase a service, you may need to provide us with certain
          details such as billing name, billing contact details (street addresses, email addresses), and payment
          instrument details.
        </p>
        <p>
          Identity verification information. Some services require you to verify your identity as part of creating an
          account to access them. We may collect information such as email addresses or phone numbers for this purpose.
        </p>
        <p>
          Communications and submissions. You may choose to provide us with information when you communicate with us
          (e.g. via email, phone, or chat for support or to inquire about our services), including when you fill out an
          online form, respond to surveys, provide feedback, post comments to our website, participate in promotions, or
          submit information through our services.
        </p>
        <p> For more information, please read our Privacy Policy. You can always come back....</p>
      </section>
      <section className='data-consent__button-group'>
        <button className='data-consent__accept-button' onClick={onClose}>
          Got it
        </button>
        <button className='data-consent__settings-button' onClick={onReviewSettings}>
          Review settings
        </button>
      </section>
    </div>
  );
}
