import sdk from '@common-shared/SDK';
import '../BypassWebsites/BypassWebsites.less';
import { Component } from 'react';
import Info from '@hotspot-shield/shared/panel/components/info';

class SecuredWebsites extends Component {
  constructor(props) {
    super();

    this.state = {
      total: 0,
      sites: [],
      active: false,
      secured: false
    };

    this.nextClick = 0;

    const result = this.getSites();
    this.state.total = result.total;
    this.state.sites = result.sites;
    this.state.active = result.active;

    if (props && props.mainScreen) this.state.secured = true;

    this.signal = sdk.proxy.secured.onChange.add(() => {
      this.setState(this.getSites());
    });
  }

  componentWillUnmount() {
    this.signal.detach();
  }

  getSites() {
    let total = 0;
    const result = [];
    let active = false;

    const sites = sdk.proxy.secured.get();

    for (const k in sites) {
      total++;

      let name = k.replace(/\.[^.]+$/, '');
      name = name.charAt(0).toUpperCase() + name.slice(1);

      result.push({
        domain: k,
        favicon: sdk.tabs.getFavicon(k),
        location: sites[k],
        name
      });

      if (k === sdk.tabs.active.domain) active = true;
    }

    storage.secured = active;

    return { total, sites: result, active };
  }

  open(domain) {
    if (Date.now() < this.nextClick) return;

    sdk.reporting.report('secured', 'open', domain);
    sdk.tabs.openPage(`http://${domain}`);
  }

  remove(domain) {
    this.nextClick = Date.now() + 500;
    sdk.reporting.report('secured', 'remove', domain);
    sdk.proxy.secured.remove(domain);
  }

  async add() {
    const tab = await sdk.tabs.getActiveWebsiteTab();
    if (tab) {
      sdk.reporting.report('secured', 'add', tab.domain);
      await sdk.proxy.secured.add(tab.domain);
    }
  }

  openSettings() {
    sdk.tabs.openPage(`${sdk.config.protocolPrefix}://${sdk.config.application.applicationId}/pages/settings.html`);
  }

  render() {
    let size;
    if (this.state.active) {
      size = (
        <div className='open' onClick={this.openSettings.bind(this)}>
          {locales.get('browser_settings_open')}
        </div>
      );
    } else if (this.state.total > 0) {
      size = `${locales.get('secured_websites_showing')} `;
      size += Math.min(this.state.total, 7);
      size += ` ${locales.get('secured_websites_of')} `;
      size += this.state.total;
      size = <div className='size'>{size}</div>;
    }

    return (
      <div id='mainSecuredWebsites' className={this.state.secured ? 'bottomBorder' : 'topBorder'}>
        <div className='title'>
          <div className='settings'>
            {locales.get('secured_websites_title')}
            <Info text={locales.get('main_secured_tooltip')} />
          </div>
          {size}
        </div>
        <div className='elementList'>
          {this.renderSites()}
          <div className='clear' />
        </div>
      </div>
    );
  }

  renderSites() {
    const limit = this.state.active ? 8 : 7;
    const result = [];
    let i = 0;

    for (const k in this.state.sites) {
      const site = this.state.sites[k];

      const text = site.favicon ? undefined : site.name.substr(0, 2).toUpperCase();
      const style = site.favicon ? { backgroundImage: `url(${site.favicon})` } : {};

      const element = (
        <div className='element site' key={site.domain} onClick={this.open.bind(this, site.domain)}>
          <div className={`favicon${text ? ' noFavicon' : ''}`} style={style}>
            <span>{text}</span>
          </div>
          <div className='domain'>{site.name.substr(0, 8)}</div>
          <div className='remove' onClick={this.remove.bind(this, site.domain)}>
            <div>+</div>
          </div>
        </div>
      );

      if (i++ >= limit && site.domain !== sdk.tabs.active.domain) continue;

      if (site.domain === sdk.tabs.active.domain) result.unshift(element);
      else result.push(element);
    }

    if (result.length > limit) result.pop();

    if (!this.state.active) {
      result.push(
        <div className='element' key='add' onClick={this.add.bind(this)}>
          <div className='favicon noFavicon add'>+</div>
          <div className='domain'>{locales.get('secured_websites_add')}</div>
        </div>
      );
    }

    return result;
  }
}

export default SecuredWebsites;
