import sdk from '@common-shared/SDK';
import './Menu.less';

import { Component } from 'react';

import Back from '@hotspot-shield/shared/panel/components/back';
import SignIn from './SignIn';
import SignUp from './SignUp';
import RestorePassword from './RestorePassword';
import MyAccount from './MyAccount';
import Settings from './Settings';
import About from './About';
import Help from './Help';

class Menu extends Component {
  constructor(props) {
    super(props);

    this.state = {
      screen: ''
    };
  }

  componentDidUpdate(prevProps) {
    if (this.props.show && prevProps.show !== this.props.show) {
      sdk.reporting.report('screen', 'menu');
    }

    if (prevProps.screenName !== this.props.screenName && this.props.screenName !== '') {
      this.changeScreen(this.props.screenName);
      this.props.resetScreenName();
    }
  }

  back() {
    this.setState({ screen: '' });
  }

  changeScreen(screen) {
    this.setState({ screen });
  }

  click(affr) {
    const urls = {
      rate: sdk.config.promo.rateUs.url,
      fb: 'https://www.facebook.com/sharer/sharer.php?u=https%3A//www.facebook.com/hotspotshield',
      tw: 'https://twitter.com/home?status=http%3A//twitter.com/hotspotshield',
      debug: `${sdk.config.protocolPrefix}://${sdk.config.application.applicationId}/pages/debug.html`
    };

    sdk.reporting.report('click', 'menu', affr).then(() => {
      if (affr === 'mail') {
        let link = 'mailto:?Subject=Get%20help%20accessing%20blocked%20content%21&body=';
        link += encodeURIComponent(
          `Hi there,

I'd like to share with you an awesome way you can stay safe and secured while browsing in Chrome. It's a free Chrome Extension so give it a try https://chrome.google.com/webstore/detail/hotspot-shield-vpn-free-p/${sdk.config.application.applicationId}.

Cheers!`
        );
        sdk.tabs.openPage(link);
      } else {
        sdk.tabs.openPage(urls[affr]);
      }
    });
  }

  render() {
    let content;
    let screen;

    content = (
      <div className='content'>
        {sdk.user.status.authorized && (
          <div
            className='row'
            onClick={() => {
              this.changeScreen('myAccount');
            }}
          >
            <div className='icon' style={{ backgroundImage: "url('/static/assets/menu/account.svg')" }} />
            {locales.get('menu_account')}
          </div>
        )}

        <div
          className='row'
          onClick={() => {
            this.changeScreen('settings');
          }}
        >
          <div className='icon' style={{ backgroundImage: "url('/static/assets/menu/settings.svg')" }} />
          {locales.get('menu_settings')}
        </div>
        <div
          className='row'
          onClick={() => {
            this.changeScreen('help');
          }}
        >
          <div className='icon' style={{ backgroundImage: "url('/static/assets/menu/help.svg')" }} />
          {locales.get('menu_help')}
        </div>
        <div
          className='row'
          onClick={() => {
            this.changeScreen('about');
          }}
        >
          <div className='icon' style={{ backgroundImage: "url('/static/assets/menu/about.svg')" }} />
          {locales.get('menu_about')}
        </div>
        {!sdk.user.status.elite && (
          <div
            className='row'
            onClick={() => {
              sdk.user.openPopUp('menuUpgrade');
            }}
          >
            <div className='icon' style={{ backgroundImage: "url('/static/assets/menu/upgrade.svg')" }} />
            {locales.get('menu_upgrade')}
          </div>
        )}
        <div className='row' onClick={this.click.bind(this, 'rate')}>
          <div className='icon' style={{ backgroundImage: "url('/static/assets/menu/rate.svg')" }} />
          {locales.get('menu_rateus')}
        </div>
        {sdk.config.debug && (
          <div className='row' onClick={this.click.bind(this, 'debug')}>
            <div
              className='icon'
              style={{ backgroundImage: "url('/static/assets/menu/debug.png')", backgroundSize: 'auto 100%' }}
            />
            Debug
          </div>
        )}
      </div>
    );

    if (this.state.screen === 'signIn') {
      screen = <SignIn back={this.back.bind(this)} changeScreen={this.changeScreen.bind(this)} />;
    } else if (this.state.screen === 'signUp') {
      screen = <SignUp back={this.back.bind(this)} changeScreen={this.changeScreen.bind(this)} />;
    } else if (this.state.screen === 'restorePassword') {
      screen = <RestorePassword back={this.back.bind(this)} changeScreen={this.changeScreen.bind(this)} />;
    } else if (this.state.screen === 'myAccount') {
      screen = <MyAccount back={this.back.bind(this)} changeScreen={this.changeScreen.bind(this)} />;
    } else if (this.state.screen === 'settings') {
      screen = <Settings back={this.back.bind(this)} changeScreen={this.changeScreen.bind(this)} />;
    } else if (this.state.screen === 'about') {
      screen = <About back={this.back.bind(this)} changeScreen={this.changeScreen.bind(this)} />;
    } else if (this.state.screen === 'help') {
      screen = <Help back={this.back.bind(this)} changeScreen={this.changeScreen.bind(this)} />;
    }

    return (
      <div id='menuContainer' className={this.props.show ? 'active' : ''}>
        <div className='bg' onClick={() => this.props.toggleMenu()} />
        <div className='container'>
          {!sdk.user.status.authorized && (
            <div
              className='signInButton blueButton'
              onClick={() => {
                this.setState({ screen: 'signIn' });
              }}
            >
              {locales.get('sign_in')}
            </div>
          )}
          {sdk.user.status.authorized && (
            <div className='statusContainer'>
              <div className='title'>
                {locales.get('name')}
                {sdk.user.status.elite && <div className='elite'>{locales.get('elite')}</div>}
              </div>
              <div className='email'>{sdk.user.status.login}</div>
              <Back
                theme='white'
                direction='forvard'
                onClick={() => {
                  this.changeScreen('myAccount');
                }}
              />
            </div>
          )}
          {content}
          <div className='shareFooter'>
            {locales.get('menu_share')}
            <div className='apps'>
              <img src='/static/assets/menu/fb.svg' onClick={this.click.bind(this, 'fb')} />
              <img src='/static/assets/menu/tw.svg' onClick={this.click.bind(this, 'tw')} />
              <img src='/static/assets/menu/mail.svg' onClick={this.click.bind(this, 'mail')} />
            </div>
          </div>
        </div>
        {screen}
      </div>
    );
  }
}

export default Menu;
