import sdk from '@common-shared/SDK';
import './Tooltips.less';
import { Component } from 'react';

const TOOLTIPS_COUNT = 4;
const TOOLTIPS_SHOWED = 'Panel.tooltipsShowed';

class Tooltips extends Component {
  constructor() {
    super();

    this.state = {
      tooltip: 0
    };
  }

  close() {
    sdk.storage.set(TOOLTIPS_SHOWED, true).then(() => {
      panel.showScreen();
    });
  }

  changeTooltip(i) {
    if (i === this.state.tooltip) return;

    this.setState({ tooltip: i });
  }

  render() {
    const texts = [
      <div className='text' key={0}>
        <div className='row'>
          <div>1.</div>
          {locales.get('tooltip_text_1_1')}
        </div>
        <div className='row'>
          <div>2.</div>
          {locales.get('tooltip_text_1_2')}
        </div>
        <div className='row'>
          <div>3.</div>
          {locales.get('tooltip_text_1_3')}
        </div>
      </div>,
      <div className='text' key={1}>
        <div className='row'>
          <div>1.</div>
          {locales.get('tooltip_text_2_1')}
        </div>
        <div className='row'>
          <div>2.</div>
          {locales.get('tooltip_text_2_2')}
        </div>
        <div className='row'>
          <div>3.</div>
          {locales.get('tooltip_text_2_3')}
        </div>
        <div className='row'>
          <div>4.</div>
          {locales.get('tooltip_text_2_4')}
        </div>
        <div className='row'>
          <div>5.</div>
          {locales.get('tooltip_text_2_5')}
        </div>
        <div className='row'>
          <div>6.</div>
          {locales.get('tooltip_text_2_6')}
        </div>
      </div>,
      <div className='text' key={2}>
        <div className='row'>
          <div>1.</div>
          {locales.get('tooltip_text_3_1')}
        </div>
        <div className='row'>
          <div>2.</div>
          {locales.get('tooltip_text_3_2')}
        </div>
        <div className='row'>
          <div>3.</div>
          {locales.get('tooltip_text_3_3')}
        </div>
        <div className='row'>
          <div>4.</div>
          {locales.get('tooltip_text_3_4')}
        </div>
      </div>,
      <div className='text' key={3}>
        <div className='row'>
          <div>1.</div>
          {locales.get('tooltip_text_4_1')}
        </div>
        <div className='row'>
          <div>2.</div>
          {locales.get('tooltip_text_4_2')}
        </div>
        <div className='row'>
          <div>3.</div>
          {locales.get('tooltip_text_4_3')}
        </div>
        <div className='row'>
          <div>4.</div>
          {locales.get('tooltip_text_4_4')}
        </div>
        <div className='row'>
          <div>5.</div>
          {locales.get('tooltip_text_4_5')}
        </div>
      </div>
    ];

    const titles = [
      <div className='title' key={5}>
        {locales.get('tooltip_title_1')}
      </div>,
      <div className='title' key={6}>
        {locales.get('tooltip_title_2')}
      </div>,
      <div className='title' key={7}>
        {locales.get('tooltip_title_3')}
      </div>,
      <div className='title' key={8}>
        {locales.get('tooltip_title_4')}
      </div>
    ];

    return (
      <div id='screenTooltips'>
        {this.state.tooltip !== 4 && (
          <div
            className='imgContainer'
            onClick={this.imgClick.bind(this)}
            style={{ backgroundImage: `url("/static/assets/tooltips/t${this.state.tooltip + 1}.png")` }}
          />
        )}
        {this.state.tooltip === 4 && this.renderTwoComponentTooltip()}
        <div className={`navigation num${this.state.tooltip}`}>
          {this.state.tooltip !== 4 && titles[this.state.tooltip]}
          {this.state.tooltip !== 4 && texts[this.state.tooltip]}
          <div className='footer'>
            {this.state.tooltip !== 0 && (
              <div
                className='previous'
                onClick={() => {
                  this.changeTooltip(this.state.tooltip - 1);
                }}
              >
                {locales.get('tooltip_previous')}
              </div>
            )}
            <div className='circules'>
              <div
                className={`circle${this.state.tooltip === 0 ? ' active' : ''}`}
                onClick={this.changeTooltip.bind(this, 0)}
              />
              <div
                className={`circle${this.state.tooltip === 1 ? ' active' : ''}`}
                onClick={this.changeTooltip.bind(this, 1)}
              />
              <div
                className={`circle${this.state.tooltip === 2 ? ' active' : ''}`}
                onClick={this.changeTooltip.bind(this, 2)}
              />
              <div
                className={`circle${this.state.tooltip === 3 ? ' active' : ''}`}
                onClick={this.changeTooltip.bind(this, 3)}
              />
              <div
                className={`circle${this.state.tooltip === 4 ? ' active' : ''}`}
                onClick={this.changeTooltip.bind(this, 4)}
              />
            </div>
            {this.state.tooltip < TOOLTIPS_COUNT && (
              <div
                className='next'
                onClick={() => {
                  this.changeTooltip(this.state.tooltip + 1);
                }}
              >
                {locales.get('tooltip_next')}
              </div>
            )}
            {this.state.tooltip === TOOLTIPS_COUNT && (
              <div className='next' onClick={this.close.bind(this)}>
                {locales.get('tooltip_done')}
              </div>
            )}
          </div>
        </div>
      </div>
    );
  }

  imgClick() {
    if (this.state.tooltip < TOOLTIPS_COUNT) this.changeTooltip(this.state.tooltip + 1);
    else this.close();
  }

  renderTwoComponentTooltip() {
    return (
      <div>
        <div
          className='imgContainer'
          style={{ backgroundImage: 'url("/static/assets/tooltips/t5_2.png")', height: '83px' }}
          onClick={this.imgClick.bind(this)}
        />
        <div className='twoComponentText' style={{ backgroundColor: '#fff' }}>
          <div className='spoofTitle'>Sword</div>
          <div className='spoofText'>Keep your real web activity private by feeding web trackers fake web activity</div>
          <div className='row'>
            <div>1.</div>Stop web trackers from login your real activity
          </div>
          <div className='row'>
            <div>2.</div>Keep your personal information away from hackers and identity thieves
          </div>
          <div className='row'>
            <div>3.</div>Create a safer, more private browsing experience
          </div>
        </div>
        <div
          className='imgContainer'
          style={{ backgroundImage: 'url("/static/assets/tooltips/t5_1.png")', height: '132px' }}
          onClick={this.imgClick.bind(this)}
        />
        <div className='twoComponentText second' style={{ backgroundColor: '#fff' }}>
          Learn more by visiting "Browser settings" within "Configuration Settings"
        </div>
      </div>
    );
  }
}

export default Tooltips;
