import sdk from '@common-shared/SDK';

export const downloadDesktop = e => {
  if (e) {
    e.preventDefault();
  }

  sdk.reporting.report('welcome', 'click-download').then(() => {
    if (['Macintosh', 'MacIntel', 'MacPPC', 'Mac68K'].indexOf(window.navigator.platform) !== -1) {
      sdk.tabs.openPage(
        `https://control.kochava.com/v1/cpi/click?campaign_id=kohotspot-shield-e1z5mebc85b959b629ea&network_id=7450&site_id=1&device_id=${sdk.user.hash}`
      );
    } else {
      sdk.tabs.openPage(
        `https://control.kochava.com/v1/cpi/click?campaign_id=kohotspot-shield-2oo5abbd4627161b66&network_id=7450&site_id=1&device_id=${sdk.user.hash}`
      );
    }
  });
};
