import './DataLimit.less';
import classNames from 'classnames';

function DataLimit({ amount, total, isLow = false, onClick }) {
  const percentage = Math.round((amount / total) * 100);
  if (amount < 1) {
    return (
      <div className={classNames('data-limit', { 'data-limit--low': isLow })}>
        <img className='data-limit__img' src='/static/assets/icons/data-limit/data-limit-no-left.png' />
        <div className='data-limit__description'>No free daily data left</div>
        <div className='data-limit__add' onClick={onClick}>
          + Add
        </div>
      </div>
    );
  }
  return (
    <div className={classNames('data-limit', { 'data-limit--low': isLow })}>
      <div className='data-limit__radial-progress' data-progress={percentage}>
        <div className='data-limit__circle'>
          <div className='mask full'>
            <div className='fill' />
          </div>
          <div className='mask half'>
            <div className='fill' />
            <div className='fill fix' />
          </div>
          <div className='shadow' />
        </div>
        <div className='inset'>
          <div className='percentage' />
        </div>
      </div>
      <div className='data-limit__amount'>{amount} MB</div>
      <div className='data-limit__description'>of free daily data left</div>
      <div className='data-limit__add' onClick={onClick}>
        + Add
      </div>
    </div>
  );
}

export default DataLimit;
