import sdk from '@common-shared/SDK';
import { Component } from 'react';

import DataConsentBanner from 'shared/panel/components/data-consent-banner';
import { DataConsent } from 'shared/panel/screens/DataConsent';
import { isFirefox } from '@common-shared/BrowserDetection';
import { DATA_CONSENT_ACCEPTED } from '@common-shared/Utils/StorageKeys';
import isBoolean from 'lodash/isBoolean';
import Main from './screens/Main';
import Loading from './screens/Loading';
import Settings from './screens/Settings';
import Tooltips from './screens/Tooltips';
import Unavailable from './screens/Unavailable';
import { SurveyScreen } from './screens/Survey';

class Controller extends Component {
  constructor() {
    super();

    this.state = {
      screen: storage.panelLoaded ? 'fastLoading' : 'fullLoading'
    };
  }

  componentDidUpdate(prevProps, prevState) {
    if (['fastLoading', 'fullLoading'].includes(this.state.screen)) return;
    if (prevState.screen !== this.state.screen) {
      sdk.reporting.report('screen', this.state.screen);
    }
  }

  toMain = () => this.setState({ screen: 'main' });

  render() {
    switch (this.state.screen) {
      case 'fullLoading':
      case 'fastLoading':
        return <Loading type={this.state.screen} />;
      case 'main':
        if (isFirefox && !isBoolean(sdk.storage.get(DATA_CONSENT_ACCEPTED))) {
          return <DataConsent onClose={this.toMain} onReviewSettings={() => browser.runtime.openOptionsPage()} />;
        }
        return (
          <>
            <Main />
            {isFirefox && <DataConsentBanner />}
          </>
        );
      case 'survey':
        return <SurveyScreen onClick={this.toMain} onClose={this.toMain} />;
      case 'tooltips':
        return <Tooltips />;
      case 'unavailable':
        return <Unavailable />;
      case 'locations':
      case 'modes':
        return <Settings screen={this.state.screen} />;
    }
  }
}

export default Controller;
