import sdk from '@common-shared/SDK';
import './About.less';

import { Component } from 'react';

import Back from '@hotspot-shield/shared/panel/components/back';

class Settings extends Component {
  componentDidMount() {
    sdk.reporting.report('screen', 'about');
  }

  click(affr) {
    const urls = {
      about: 'https://aura.com/about',
      policy: `https://aura.com/legal/privacy-policy/?utm_source=${sdk.config.elite.popupUtm}&utm_medium=Free&utm_campaign=PrivacyPolicy`,
      terms: `https://www.hotspotshield.com/terms/?utm_source=${sdk.config.elite.popupUtm}&utm_medium=Free&utm_campaign=TermsConditions`,
      facebook: 'https://www.facebook.com/hotspotshield',
      twitter: 'http://twitter.com/hotspotshield'
    };

    sdk.reporting.report('click', 'about', affr).then(() => {
      sdk.tabs.openPage(urls[affr]);
    });
  }

  render() {
    const data = [
      [locales.get('menu_about'), 'about'],
      [locales.get('about_privacy'), 'policy'],
      [locales.get('about_terms'), 'terms'],
      [locales.get('about_like'), 'facebook'],
      [locales.get('about_follow'), 'twitter']
    ];
    const content = [];

    for (const k in data) {
      content.push(
        <div className='row' key={k} onClick={this.click.bind(this, data[k][1])}>
          <div className='label' />
          <div className='content'>
            {data[k][0]}
            <div className='right'>Open</div>
          </div>
        </div>
      );
    }

    return (
      <div className='fullSizeScreen menuMyAccount'>
        <div className='whiteContainer'>
          <div className='title'>{locales.get('menu_about')}</div>
          <Back onClick={() => this.props.back()} theme='dark' />
          {content}
        </div>
        <div className='footerVersion'>
          {locales.get('version')} {sdk.config.application.version}
          <br />
          {locales.get('powered')}
        </div>
      </div>
    );
  }
}

export default Settings;
