import sdk from '@common-shared/SDK';
import Info from '@hotspot-shield/shared/panel/components/info';

function BrowserSettings() {
  return (
    <div className='block'>
      <div className='content'>
        <div className='row'>
          {locales.get('browser_settings_title')}
          <Info text={locales.get('modes_tooltips_settings')} />
          <div
            className='right'
            onClick={() => {
              sdk.tabs.openPage(
                `${sdk.config.protocolPrefix}://${sdk.config.application.applicationId}/pages/settings.html`
              );
            }}
          >
            {locales.get('browser_settings_open')}
          </div>
        </div>
      </div>
    </div>
  );
}

export default BrowserSettings;
