import sdk from '@common-shared/SDK';
import './Unblocked.less';
import { Component } from 'react';

const INTERVAL = 5 * 1000;

class Unblocked extends Component {
  constructor(props) {
    super(props);

    this.state = {
      unblocked: sdk.sitesCounter()
    };

    this.interval = setInterval(() => {
      this.setState({
        unblocked: sdk.sitesCounter()
      });
    }, INTERVAL);
  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }

  render() {
    return (
      <div id='mainUnblockedWebsites' className='topBorder'>
        {locales.get('unblocked_title')} <div className='counter'>{this.state.unblocked}</div>
      </div>
    );
  }
}

export default Unblocked;
