import sdk from '@common-shared/SDK';
import './SignIn.less';

import { Component } from 'react';

import Input from '@hotspot-shield/shared/panel/components/input';

import Back from '@hotspot-shield/shared/panel/components/back';

class SignIn extends Component {
  constructor(props) {
    super(props);

    this.state = {
      form: {
        email: false,
        password: false
      },
      messages: {
        email: false,
        password: false
      },
      disabled: false,
      message: ''
    };

    this.form = {
      email: '',
      password: ''
    };
  }

  componentDidMount() {
    sdk.reporting.report('screen', 'signin');
  }

  fieldChanged(field, value) {
    this.form[field] = value.trim();
  }

  submitForm() {
    const state = this.state;
    state.form = {
      email: false,
      password: false
    };

    state.messages = {
      email: false,
      password: false
    };

    state.disabled = false;
    state.message = '';

    let valid = true;
    if (this.form.email === '') {
      state.form.email = true;
      valid = false;
      state.message = locales.get('fill');
    }

    if (this.form.password === '') {
      state.form.password = true;
      valid = false;
      state.message = locales.get('fill');
    }

    if (!valid) {
      return this.setState(state);
    }

    state.disabled = true;

    this.setState(state, () => {
      const state = this.state;
      state.disabled = false;

      sdk.user
        .signIn(this.form.email, this.form.password)
        .then(() => {
          return this.setState(state, () => this.props.changeScreen(''));
        })
        .catch(response => {
          state.message = response.error_message ? response.error_message : 'Internal server error';
          state.form = {
            email: true,
            password: true
          };

          return this.setState(state);
        });
    });
  }

  render() {
    return (
      <div className='fullSizeScreen menuSignIn'>
        <div className='title'>{locales.get('sign_in')}</div>
        <Back onClick={() => this.props.back()} theme='dark' />
        {this.state.message !== '' && <div className='message'>{this.state.message}</div>}
        <div className='logo' />
        <div className='email'>
          <Input
            label={locales.get('label_email')}
            value={this.form.email}
            notValid={this.state.form.email}
            message={this.state.messages.email}
            onEnter={this.submitForm.bind(this)}
            disabled={this.state.disabled}
            onChange={this.fieldChanged.bind(this, 'email')}
          />
        </div>
        <div className='password'>
          <Input
            label={locales.get('label_enterpassword')}
            type='password'
            value={this.form.password}
            notValid={this.state.form.password}
            message={this.state.messages.password}
            onEnter={this.submitForm.bind(this)}
            disabled={this.state.disabled}
            onChange={this.fieldChanged.bind(this, 'password')}
          />
        </div>
        <div className='forgot' onClick={() => this.props.changeScreen('restorePassword')}>
          {locales.get('restore_forgot')}
        </div>
        {this.state.disabled && <div className='signInBtn blueButton disabled'>{locales.get('wait')}</div>}
        {!this.state.disabled && (
          <div className='signInBtn blueButton' onClick={this.submitForm.bind(this)}>
            {locales.get('sign_in')}
          </div>
        )}
        <div className='noAccount'>
          {locales.get('signin_noaccount')}{' '}
          <span onClick={() => this.props.changeScreen('signUp')}>{locales.get('sign_up')}</span>
        </div>
      </div>
    );
  }
}

export default SignIn;
