import './Settings.less';

import Modes from './Modes';
import BrowserSettings from './BrowserSettings';
import Locations from './Locations';

function Settings({ show, screen, toggleSettings }) {
  let content = [];
  if (screen === 'modes') {
    content = [<Modes key={0} />, <BrowserSettings key={1} />, <Locations key={2} />];
  } else {
    content = [<Locations key={0} />, <Modes key={1} />, <BrowserSettings key={2} />];
  }

  return (
    <div id='screenSettings' className={show ? 'active' : ''}>
      <div className='title'>
        {locales.get('configuration_title')}
        <div className='close' onClick={() => toggleSettings()} />
      </div>
      <div className='contentContainer scroll2'>
        {content}
        <div style={{ height: '90px' }} />
      </div>
    </div>
  );
}

export default Settings;
