import './rounded-switch.less';
import classnames from 'classnames';

function RoundedSwitch({ className, value, onChange }) {
  return (
    // eslint-disable-next-line jsx-a11y/label-has-associated-control
    <label className={classnames('rounded-switch', className)}>
      {/* eslint-disable-next-line react/jsx-props-no-spreading */}
      <input className='rounded-switch__checkbox' type='checkbox' checked={value} onChange={() => onChange(!value)} />
      <span className='rounded-switch__slider' />
    </label>
  );
}

export default RoundedSwitch;
