import sdk from '@common-shared/SDK';
import { t } from '@common-shared/Translation';

import text from './text';

const LANGUAGE = 'Locales.language';

class locales {
  constructor() {
    console.log(sdk);
    this.restoreLanguage();
  }

  get(name) {
    return t(name);
  }

  getActiveLanguage() {
    return this.language;
  }

  getAllLanguages() {
    return sdk.lokalise.getAllLanguages();
  }

  restoreLanguage() {
    let systemLanguage;
    try {
      const locale = window.navigator.userLanguage || window.navigator.language;
      systemLanguage = (locale || '').substr(0, 2) || 'en';
    } catch (exception) {
      systemLanguage = 'en';
    }
    if (!text[systemLanguage]) systemLanguage = 'en';
    this.systemLanguage = systemLanguage;

    if (!sdk || !sdk.storage) {
      this.language = this.systemLanguage;
    } else {
      this.language = this.systemLanguage;
      // this.language = sdk.storage.get(LANGUAGE) ? sdk.storage.get(LANGUAGE) : this.systemLanguage;
    }

    if (document.getElementsByTagName('body') && document.getElementsByTagName('body')[0]) {
      document.getElementsByTagName('body')[0].className = this.language;
    }
  }

  getLanguage() {
    return sdk.storage.get(LANGUAGE) ? this.language : this.systemLanguage;
  }

  setLanguage(language) {
    return sdk.storage.set(LANGUAGE, language).then(() => {
      this.language = language;

      if (document.getElementsByTagName('body') && document.getElementsByTagName('body')[0]) {
        document.getElementsByTagName('body')[0].className = this.language;
      }
    });
  }
}

export default locales;
