import './toggle.less';

import { useEffect } from 'react';
import { useToggle } from 'react-use';

function Toggle({ enabled = false, onChange = () => {} }) {
  const [isToggled, toggle] = useToggle(enabled);
  useEffect(() => {
    toggle(enabled);
  }, [enabled]);
  const onClick = () => {
    onChange(!isToggled);
    toggle(!isToggled);
  };

  return (
    <div className={`toggle ${isToggled ? 'on' : 'off'}`} onClick={onClick}>
      <div className='switch' />
    </div>
  );
}

export default Toggle;
