/* eslint-disable jsx-a11y/control-has-associated-label */
import sdk from '@common-shared/SDK';
import './Button.less';
import { Component } from 'react';

import classNames from 'classnames';

import bodymovin from 'bodymovin';
import Connection from '@hotspot-shield/shared/panel/components/Connection';
import { tf } from '@common-shared/Translation';
import Timer from './Timer';
import animation from './animation';

class Button extends Component {
  constructor() {
    super();

    this.connection = new Connection();
  }

  click() {
    switch (storage.status) {
      case 'disconnected':
        this.setStatus('connecting');
        this.connection.connect();
        break;
      case 'connected':
        this.setStatus('disconnectingS1');
        this.connection.disconnect();
        break;
    }
  }

  componentDidUpdate() {
    this.setAnimation();
  }

  componentDidMount() {
    this.setAnimation();
  }

  setAnimation() {
    switch (storage.status) {
      case 'disconnected':
        return this.renderAnimation(animation[0]);
      case 'connecting':
        return this.renderAnimation(animation[1], animation[3]).then(() => {
          this.animation.destroy();
          this.setStatus('postConnecting');
        });
      case 'disconnectingS2':
        return this.renderAnimation(animation[6]).then(() => {
          this.animation.destroy();
          this.setStatus('disconnected');
        });
      default:
    }
  }

  renderAnimation(step1, step2) {
    if (this.animation) this.animation.destroy();
    return new Promise(resolve => {
      const params = {
        container: document.getElementsByClassName('button')[0],
        renderer: 'svg',
        loop: false,
        autoplay: true,
        animationData: step1
      };
      this.animation = bodymovin.loadAnimation(params);

      // eslint-disable-next-line no-return-assign
      return (this.animation.onComplete = () => {
        if (step2) return resolve(this.renderAnimation(step2));

        resolve();
      });
    });
  }

  render() {
    switch (storage.status) {
      case 'disconnected':
        return this.renderDisconnected();
      case 'connecting':
        return this.renderConnecting();
      case 'postConnecting':
        setTimeout(() => {
          this.setStatus('connected');
        }, 400);
        return this.renderPostConnecting();
      case 'connected':
        return this.renderConnected();
      case 'disconnectingS1':
        setTimeout(() => {
          this.setStatus('disconnectingS2');
        }, 400);
        return this.renderDisconnectingS1();
      case 'disconnectingS2':
        return this.renderDisconnectingS2();
      default:
        return <div />;
    }
  }

  renderConnected() {
    return (
      <div id='mainButton' className='connected'>
        <div id='timer' className='fadeIn'>
          <Timer />
          {locales.get('connection_button_stop')}
        </div>
        <div className='button connected' onClick={this.click.bind(this, 'connected')} />
      </div>
    );
  }

  renderConnecting() {
    return (
      <div id='mainButton' className='connecting'>
        <div className='button connecting' />
        <div className='description fadeIn linebreak'>{locales.get('connection_button_connecting')}</div>
      </div>
    );
  }

  renderDisconnectingS1() {
    return (
      <div id='mainButton' className='disconnectingS1'>
        <div className='button disconnectingS1' />
      </div>
    );
  }

  renderDisconnectingS2() {
    return (
      <div id='mainButton' className='disconnectingS2'>
        <div className='button disconnectingS2' />
      </div>
    );
  }

  renderPostConnecting() {
    return (
      <div id='mainButton' className='postConnecting'>
        <div className='button postConnecting'>
          <div className='checkbox' />
        </div>
        <div className='description fadeIn linebreak postConnectingText'>
          {locales.get('connection_button_connected')}
        </div>
      </div>
    );
  }

  renderDisconnected() {
    const isLimitLeft = sdk.limitUsage() - sdk.bandwidthCounter().day.bandwidth.toFixed(0) <= 0;

    return (
      <div id='mainButton'>
        <div
          className={classNames('button disconnected', { disabled: isLimitLeft })}
          onClick={this.click.bind(this, 'disconnected')}
        />
        <div className='description fadeIn linebreak'>{locales.get('connection_button_disconnected')}</div>
        <div className='gdpr'>
          {tf(
            'terms_agree_by_using',
            <br />,
            <a href='https://www.hotspotshield.com/terms/' target='_blank' rel='noopener noreferrer' />,
            <a href='https://aura.com/legal/privacy-policy' target='_blank' rel='noopener noreferrer' />
          )}
        </div>
      </div>
    );
  }

  setStatus(status) {
    storage.status = status;
    signals.onStatusChanged.dispatch();
  }
}

export default Button;
