import sdk from '@common-shared/SDK';
import './Connection.less';
import { Component } from 'react';

const INTERVAL = 1000;

class Connection extends Component {
  constructor() {
    super();

    this.state = {
      speed: sdk.speed.get()?.toFixed(2) || '0.00',
      bandwidth: sdk.bandwidthCounter().day.bandwidth
    };

    this.interval = setInterval(() => {
      this.setState({
        speed: sdk.speed.get()?.toFixed(2) || '0.00',
        bandwidth: sdk.bandwidthCounter().day.bandwidth
      });
    }, INTERVAL);
  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }

  render() {
    let type = 'MB';
    let bandwidth = this.state.bandwidth.toFixed(2);
    if (bandwidth > 1024) {
      type = 'GB';
      bandwidth = (bandwidth / 1024).toFixed(2);
    }

    return (
      <div id='mainConnection' className={`topBorder${storage.firstAnimation}`}>
        <div className='container float'>
          <div className='number'>{bandwidth}</div>
          <div className='text'>
            {locales.get('connection_bandwidth')}, {type}
          </div>
        </div>
        <div className='delimiter float rightBorder' />
        <div className='container float'>
          <div className='number'>{this.state.speed}</div>
          <div className='text'>{locales.get('connection_speed')}, Mbps</div>
        </div>
        <div className='clear' />
      </div>
    );
  }
}

export default Connection;
