import sdk from '@common-shared/SDK';
import RoundedSwitch from '@hotspot-shield/shared/panel/components/rounded-switch';
import { useCallback, useEffect, useState } from 'react';
import './data-consent-banner.less';
import { useLogger } from 'react-use';
import { DATA_CONSENT_ACCEPTED } from '@common-shared/Utils/StorageKeys';

function DataConsentBanner() {
  const [baseValue, setBaseValue] = useState(null);
  const [accepted, setAccepted] = useState(false);
  const [isClose, setClose] = useState(false);
  useEffect(() => {
    const value = !!sdk.storage.get(DATA_CONSENT_ACCEPTED);
    setAccepted(value);
    setBaseValue(value);
  }, []);

  const onAcceptedChange = useCallback(value => {
    sdk.storage.set(DATA_CONSENT_ACCEPTED, value).then(() => setAccepted(value));
  }, []);

  useEffect(() => {
    if (accepted && baseValue !== accepted) {
      const timeout = setTimeout(() => {
        setClose(true);
      }, 2000);
      return () => clearTimeout(timeout);
    }
  }, [accepted, baseValue]);

  useLogger('DataConsentBanner accepted, baseValue', accepted, baseValue);

  if (isClose || baseValue === true) {
    return null;
  }

  return (
    <div className='data-consent-banner'>
      <h2 className='data-consent-banner__title'>Provide access to app usage data</h2>
      <p className='data-consent-banner__text'>
        Help Hotspot Shield to become better by sharing your in app usage. This data is fully anonymized and GDPR
        compliant. We don't sell your personal data to other providers.
      </p>
      <RoundedSwitch className='data-consent-banner__toggle' value={accepted} onChange={onAcceptedChange} />
      {accepted ? (
        <button className='data-consent-banner__button data-consent-banner__gratitude' onClick={() => setClose(true)}>
          Thank you!
        </button>
      ) : (
        <button className='data-consent-banner__button data-consent-banner__close' onClick={() => setClose(true)}>
          Close
        </button>
      )}
    </div>
  );
}

export default DataConsentBanner;
