import sdk from '@common-shared/SDK';
import './SignUp.less';

import { Component } from 'react';

import Input from '@hotspot-shield/shared/panel/components/input';

import Back from '@hotspot-shield/shared/panel/components/back';

class SignUp extends Component {
  constructor(props) {
    super(props);

    this.state = {
      form: {
        email: false,
        password: false
      },
      disabled: false,
      message: ''
    };

    this.form = {
      email: '',
      password: ''
    };
  }

  componentDidMount() {
    sdk.reporting.report('screen', 'signup');
  }

  fieldChanged(field, value) {
    this.form[field] = value.trim();
  }

  submitForm() {
    const state = this.state;
    state.form = {
      email: false,
      password: false
    };
    state.disabled = false;
    state.message = '';

    let valid = true;
    if (this.form.email === '') {
      state.form.email = true;
      valid = false;
      state.message = locales.get('fill');
    }

    if (this.form.password === '') {
      state.form.password = true;
      valid = false;
      state.message = locales.get('fill');
    } else if (this.form.password.length < 6) {
      state.form.password = true;
      valid = false;
      state.message = locales.get('password6');
    }

    if (!valid) {
      return this.setState(state);
    }

    state.disabled = true;

    this.setState(state, () => {
      const state = this.state;
      state.disabled = false;

      sdk.user
        .signUp(this.form.email, this.form.password)
        .then(() => this.setState(state, () => this.props.changeScreen('')))
        .catch(response => {
          state.message = response.error_message ? response.error_message : 'Internal server error';
          if (response.error_code === 403) {
            state.form = {
              email: true,
              password: true
            };
          } else if (response.error_code === 405) {
            state.form = {
              email: true
            };
          }

          return this.setState(state);
        });
    });
  }

  click(affr) {
    const urls = {
      tos: `https://www.hotspotshield.com/terms/?utm_source=${sdk.config.elite.popupUtm}&utm_medium=Free&utm_campaign=TermsConditions`,
      policy: `https://aura.com/legal/privacy-policy/?utm_source=${sdk.config.elite.popupUtm}&utm_medium=Free&utm_campaign=PrivacyPolicy`
    };

    sdk.reporting.report('signup', `click-${affr}`).then(() => {
      sdk.tabs.openPage(urls[affr]);
    });
  }

  render() {
    return (
      <div className='fullSizeScreen menuSignUp'>
        <div className='title'>{locales.get('sign_up')}</div>
        <Back onClick={() => this.props.changeScreen('signIn')} theme='dark' />
        {this.state.message !== '' && <div className='message'>{this.state.message}</div>}
        <div className='logo' />
        <div className='email'>
          <Input
            label={locales.get('label_email')}
            value={this.form.email}
            notValid={this.state.form.email}
            onEnter={this.submitForm.bind(this)}
            disabled={this.state.disabled}
            onChange={this.fieldChanged.bind(this, 'email')}
          />
        </div>
        <div className='password'>
          <Input
            label={locales.get('label_createpassword')}
            type='password'
            value={this.form.password}
            notValid={this.state.form.password}
            onEnter={this.submitForm.bind(this)}
            disabled={this.state.disabled}
            onChange={this.fieldChanged.bind(this, 'password')}
          />
        </div>
        <div className='termsAndConditions'>
          {locales.get('signup_description')} <span onClick={this.click.bind(this, 'tos')}>{locales.get('tos')}</span>{' '}
          {locales.get('signup_description_and')}{' '}
          <span onClick={this.click.bind(this, 'policy')}>{locales.get('policy')}</span>
        </div>
        {this.state.disabled && <div className='signUpBtn disabled blueButton'>{locales.get('wait')}</div>}
        {!this.state.disabled && (
          <div className='signUpBtn blueButton' onClick={this.submitForm.bind(this)}>
            {locales.get('sign_up')}
          </div>
        )}
        <div className='noAccount'>
          {locales.get('signup_have_an_account')}{' '}
          <span onClick={() => this.props.changeScreen('signIn')}>{locales.get('sign_in')}</span>
        </div>
      </div>
    );
  }
}

export default SignUp;
